body {
  background: #000000;
}

.header {
  position: fixed;
  padding: 30px;
  width: 100%;
  background: #555;
  color: #f1f1f1;
}

.header a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
  font-size: 17px;
}

.fixedbutton {
  position: fixed;
  bottom: 0px;
  right: 0px; 
  padding-right: 16px;
  padding-bottom: 16px;
}